import { CircularProgress } from "@material-ui/core";
import React, { Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import { AuthRoute } from "../../components/AuthRoute";
import DashboardPage from "./pages/dashboard/Index";
import ModifyUserInfoPage from "./pages/modifyUserInfo/Index";
import RepairUnreceived from "./pages/repairUnreceived/Index";
import RepairReceived from "./pages/repairReceived/Index";
import CommentManagement from './pages/commentManagement/Index'
import PublishPage from "./pages/publish/Index";
import UserManagePage from "./pages/userManage/Index";
import ApplyManagePage from "./pages/applyManage/Index";
import CreditManagePage from "./pages/creditManage/Index";
import MaintenancePage from "./pages/maintenance/Index";
import NotFoundPage from "../notFound/Index";
import { AdminAccess } from "../../types";
import { makeStyles } from "@material-ui/core/styles";

const baseUrl = "/admin";

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: "100vw"
  }
}))

function RouterAdmin(): JSX.Element {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <Suspense fallback={<CircularProgress />}>
        <Switch>
          <AuthRoute
            path={`${baseUrl}/`}
            exact
            pageAccessCode={AdminAccess.Dashboard}
          >
            <DashboardPage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/modifyUserInfo`}
            pageAccessCode={AdminAccess.ModifyUserInfo}
          >
            <ModifyUserInfoPage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/repairUnreceived`}
            pageAccessCode={AdminAccess.RepairUnreceived}
          >
            <RepairUnreceived />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/repairReceived`}
            pageAccessCode={AdminAccess.RepairReceived}
          >
            <RepairReceived />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/comments`}
            pageAccessCode={AdminAccess.CommentManagement}
          >
            <CommentManagement />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/publish`}
            pageAccessCode={AdminAccess.Publish}
          >
            <PublishPage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/userManage`}
            pageAccessCode={AdminAccess.UserManage}
          >
            <UserManagePage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/applyManage`}
            pageAccessCode={AdminAccess.ApplyManage}
          >
            <ApplyManagePage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/creditManage`}
            pageAccessCode={AdminAccess.CreditManage}
          >
            <CreditManagePage />
          </AuthRoute>
          <AuthRoute
            path={`${baseUrl}/maintenance`}
            pageAccessCode={AdminAccess.Maintenance}
          >
            <MaintenancePage />
          </AuthRoute>

          {/* No Match */}
          <Route component={NotFoundPage} />
        </Switch>
      </Suspense>
    </div>
  );
}

export default RouterAdmin;
