import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import TableContainer from "@material-ui/core/TableContainer";
import { Checkbox } from "@material-ui/core";
import React, { useState } from "react";

const userStyles = makeStyles({
  root: {
    maxWidth: "100%"
  },
  table: {
    // minWidth: 200,
    // width: "fit-content",
  },
  tableBody: {
    width: "100%"
  },
  tableCell: {
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    // width: 650
  },
  selectedRow: {
    backgroundColor: 'rgba(245,255,254,0.91)',
  },
  hoverRow: {
    "&:hover": {
      backgroundColor: 'rgba(245,245,245,0.87)'
    }
  }
});


const BasicTable =  ({ columns, data, onRowClick, onRowDoubleClick}) => {
  const classes = userStyles();

  const dataFields = columns.map((column, index) => {
    return {
      field: column.field,
      width: column.width
    }
  })


  const [selectedRows, setSelectedRows] = useState([]);
  // eslint-disable-next-line
  const [hoveredRow, setHoveredRow] = useState(null);

  const handleCheckboxChange = (event, row) => {
    const selectedIndex = selectedRows.indexOf(row);
    let newSelected = [];

    if (selectedIndex === -1 || selectedIndex === undefined) { // unselected -> selected
      newSelected = newSelected.concat(selectedRows, row);
    } else { // selected -> unselected
      if (selectedIndex === 0) {
        newSelected = newSelected.concat(selectedRows.slice(1))
      } else if (selectedIndex === selectedRows.length - 1) {
        newSelected = newSelected.concat(selectedRows.slice(0, -1))
      } else {
        newSelected = newSelected.concat(
          selectedRows.slice(0, selectedIndex),
          selectedRows.slice(selectedIndex + 1)
        )
      }
    }
    setSelectedRows(newSelected)
  };

  const handleSelectAll = (event) => {
    if (event.target.checked) {
      setSelectedRows(data.map((row) => row));
    } else {
      setSelectedRows([]);
    }
  };

  const handleRowClick = (event, row) => {
    if (onRowClick) {
      onRowClick(row);
    } else {
      handleCheckboxChange(event, row)
    }
  };

  const handleRowDoubleClick = (event, row) => {
    if (onRowDoubleClick) {
      onRowDoubleClick(row);
    }
  };

  return (
    <div>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>
              <Checkbox
                indeterminate={
                  selectedRows.length > 0 && selectedRows.length < data.length
                }
                checked={selectedRows.length === data.length}
                onChange={handleSelectAll}
              />
            </TableCell>
            {columns.map((column, index) => (
              <TableCell align="left" key={index}>
                {column.displayName}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody className={classes.tableBody}>
          {data.map((row,index) => (
            <TableRow
              key={index}
              onClick={(event) => handleRowClick(event, row)}
              onDoubleClick={(event) => handleRowDoubleClick(event, row)}
              onMouseEnter={() => setHoveredRow(row)}
              onMouseLeave={() => setHoveredRow(null)}
              className={`
              ${classes.hoverRow} ${
                  selectedRows.indexOf(row) !== -1 ? classes.selectedRow : ""
                }`}
            >
              <TableCell>
                <Checkbox
                  checked={selectedRows.indexOf(row) !== -1}
                  onChange={(event) => handleCheckboxChange(event, row)}
                />
              </TableCell>
              {dataFields.map((dataField, index) => (
                <TableCell className={classes.tableCell} style={{ maxWidth: dataField.width  }} align="left" key={index}>
                  {row[dataField.field]}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
      </TableContainer>
    </div>
  );
}

export default BasicTable;
