import React, { useState } from "react";
import BasicTable from "../components/userDataTable-component";
import OperationArea from "../components/userOperation-component";
import { makeStyles } from "@material-ui/core/styles";
import DialogComponent from "../components/userOpenDialog-compent";
import { Paper } from "@material-ui/core";
import Example from "./example";

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: 20,
    padding: theme.spacing(3, 2),
    maxWidth: "100%"
  }
}));

const columns = Example.columns([]);
const data = Example.data([]);

export default function UnrepairedListTable() {
  const classes = useStyles();

  const [isDialogOpen, setDialogOpen] = useState(false)
  const [selectedData, setSelectedData] = useState(null)

  const doubleClickHandler = (row) => {
    let transData = {
      column: columns,
      data: row
    }
    setDialogOpen(true)
    setSelectedData(transData)
  };

  const handleDialogClose = () => {
    setDialogOpen(false);
    setSelectedData(null);
  };

  return (
    <Paper className={classes.root}>
      <OperationArea />
      <BasicTable 
        columns={columns} 
        data={data}
        onRowDoubleClick={doubleClickHandler}  
      />
      {isDialogOpen && (
        <DialogComponent
          rowData={selectedData}
          visible={isDialogOpen}
          handleClose={handleDialogClose}
        />
      )}
    </Paper>
  );
}
