// eslint-disable-next-line
import React, { useState } from 'react';
import { makeStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles(theme => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "25ch"
    }
  }
}));

const FormInputArea = ({ fields, data, handelChanged, isEditable }) => {
  const classes = useStyles();

  const formData = fields.map(field => {
    return {
      field: field.field,
      displayName: field.displayName,
      data: data[field.field],
      icon: null,
      multiline: field.multiline !== undefined ? field.multiline : null
    };
  });

  console.log(formData)

  return (
    <form className={classes.root} noValidate autoComplete="off">
      <div>
        {formData.map((item, index) => (
          <TextField
            id="standard-required"
            key={index}
            label={item.displayName}
            defaultValue={item.data}
            multiline={item.multiline !== undefined ? true : undefined}
            rows={item.multiline !== undefined ? item.multiline : undefined}
            InputProps={{
              readOnly: !isEditable
            }}
          />
        ))}
      </div>
    </form>
  );
};

export default FormInputArea;
