import AppBar from "@material-ui/core/AppBar";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import HomeIcon from "@material-ui/icons/Home";
import MenuIcon from "@material-ui/icons/Menu";
import React, { useContext } from "react";
import { Link } from "react-router-dom";
import menuList from "./menuList";
import RouterAdmin from "./RouterAdmin";
import { GlobalContextProps, NavbarAdminItemProps } from "../../types";
import { authInfoContext } from "../../context/GlobalState";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0
    }
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth
    }
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none"
    }
  },
  drawerPaper: {
    width: drawerWidth
  },
  content: {
    paddingTop: 64,
    flexGrow: 1,
    padding: theme.spacing(3)
  }
}));

function NavbarAdmin(): JSX.Element {
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const context = useContext<GlobalContextProps>(authInfoContext);
  const { authInfo } = context;

  const handleDrawerToggle = (): void => {
    setMobileOpen(!mobileOpen);
  };

  const handleDrawerClose = (): void => {
    if (mobileOpen) {
      setMobileOpen(!mobileOpen);
    }
  };

  function canAccess(item: NavbarAdminItemProps): boolean {
    return (authInfo.accessCode & item.pageAccessCode) !== 0;
  }

  const drawer = (
    <List>
      {menuList.map(item => {
        // if (canAccess(item)) {
        return (
          <div key={item.label}>
            <ListItem
              button
              component={Link}
              to={item.to}
              onClick={handleDrawerClose}
              disabled={!canAccess(item)} // remove
            >
              <ListItemIcon>{item.icon}</ListItemIcon>
              <ListItemText primary={item.label} />
            </ListItem>
            {item.showDivider && <Divider />}
          </div>
        );
        // }
        // return null;
      })}
    </List>
  );

  return (
    <div className={classes.root}>
      <AppBar position="fixed" color="default" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            后台
          </Typography>
          <IconButton
            component={Link}
            to="/"
            style={{ right: 0, marginLeft: "auto" }}
          >
            <HomeIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer}>
        <Hidden smUp implementation="css">
          <Drawer
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper
            }}
            ModalProps={{
              keepMounted: true // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <RouterAdmin />
      </main>
    </div>
  );
}

export default NavbarAdmin;
